rent;\n  padding: 0;\n  margin: 1.066667rem 0 1.6rem;\n}\n\n.like .list .goods {\n  margin-top: 0.266667rem;\n  padding: 0 0.346667rem;\n}\n\n.like .list .goods > div {\n  padding: 0.4rem 0;\n  display: flex;\n  justify-content: flex-start;\n  align-items: center;\n  background-color: #fff;\n  border-bottom: 0.013333rem solid #eee;\n}\n\n.like .list .goods > div .divFl img {\n  display: block;\n  width: 3.333333rem;\n  height: 3.333333rem;\n}\n\n.like .list .goods > div .divFr {\n  width: 100%;\n  flex-direction: column;\n  justify-content: space-between;\n  align-self: stretch;\n  align-items: flex-start;\n  align-content: stretch;\n}\n\n.like .list .goods > div .divFr p {\n  width: 100%;\n  color: #999;\n  text-align: left;\n}\n\n.like .list .goods > div .divFr .goodsName {\n  font-size: 0.4rem;\n  margin-bottom: 0.266667rem;\n  color: #444;\n  line-height: 0.533333rem;\n  max-height: 1.066667rem;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 2;\n  -ms-text-overflow: ellipsis;\n  text-overflow: ellipsis;\n  overflow: hidden;\n  margin-bottom: -0.533333rem;\n}\n\n.like .list .goods > div .divFr .operate {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.like .list .goods > div .divFr .operate > span b {\n  font-size: 0.426667rem;\n  margin-right: 0.133333rem;\n}\n\n.like .list .goods > div .divFr .operate > span span {\n  font-size: 0.32rem;\n  color: #999;\n}\n\n.like .list .goods > div .divFr .operate > span span span {\n  color: #333;\n}\n\n.like .list .goods > div .divFr .operate button {\n  display: flex;\n  -webkit-justify-content: center;\n  justify-content: center;\n  -webkit-align-items: center;\n  align-items: center;\n  width: 2.16rem;\n  height: 0.8rem;\n  -webkit-border-radius: 0.106667rem;\n  -moz-border-radius: 0.106667rem;\n  border-radius: 0.106667rem;\n  font-size: 0.373333rem;\n  background: linear-gradient(to right, #FF8484, #FF4862);\n}\n\n.like .list .goods > div .divFr .ope